import * as $ from 'jquery';
import { getPropertiesAsync } from './document-properties';

export async function getServerUrlAsync(): Promise<string> {
  const properties = await getPropertiesAsync();
  return properties['SGM_SYS_Server'];
}

export function getUrlParams(url: string): { [key: string]: string } {
  const params: { [key: string]: string } = {};

  if (url.indexOf('?') !== -1) {
    url = url.split('?')[1];
  }

  if (url != null && url.trim() !== '') {
    for (const param of url.split('&')) {
      const [key, value] = param.replace(/\+/g, ' ').split('=');
      params[decodeURIComponent(key)] = value === undefined ? null : decodeURIComponent(value);
    }
  }

  return params;
}

export function htmlEncode(value: string): string {
  return $('<div/>').text(value).html();
}

export async function isAccoladeDocumentAsync(): Promise<boolean> {
  const properties = await getPropertiesAsync();
  return properties['SGM_SYS_Server']
    && +properties['SGM_SYS_ProjectID'] > 0;
}

export function sleep(timeout: number): Promise<void> {
  return new Promise(resolve => {
    setTimeout(resolve, timeout);
  });
}
