import * as $ from 'jquery';
import { MicrosoftAuthenticator } from './microsoft-authenticator';

declare type HttpMethod = 'GET' | 'PATCH' | 'PUT' | 'POST' | 'DELETE';

export class MicrosoftGrapApi {
  private baseUrl: string = 'https://graph.microsoft.com';

  public getFileContentAsync(filePath: string): Promise<any> {
    return this.callApiAsync(`~/me/drive/root:${filePath}:/content`);
  }

  private async callApiAsync<T = any>(url: string, method: HttpMethod = 'GET', data?: any): Promise<T> {
    const accessToken = await new MicrosoftAuthenticator().logonAsync();
    if (!accessToken) {
      return;
    }

    return await Promise.resolve($.ajax({
      beforeSend: xhr => accessToken && xhr.setRequestHeader('Authorization', `Bearer ${accessToken}`),
      contentType: method === 'POST' || method === 'PUT' || method === 'PATCH' ? 'application/json' : undefined,
      data: method === 'POST' || method === 'PUT' || method === 'PATCH' ? JSON.stringify(data) : undefined,
      method,
      url: new URL(url.replace('~', '/v1.0'), this.baseUrl).toString()
    }));
  }
}
