import * as $ from 'jquery';
import 'jquery-validation';
import { sendMessage, showOverlay } from './dialogs';
import { fabric } from './office-ui-fabric';
import { getUrlParams } from './utilities';

export interface SaveInfo {
  author: string;
  comments: string;
  publish: boolean;
  status: string;
  statusNotes: string;
}

interface Parameters {
  [key: string]: string;
  canUpdateStatus: string;
  canUpdateStatusNotes: string;
  serverUrl?: string;
  userName?: string;
}

Office.initialize = () => {
  const $dialog = $('.app-Dialog');
  // tslint:disable:no-unused-expression
  $('.ms-Overlay').each((_, overlay) => void new fabric.Overlay(overlay));
  $('.ms-TextField').each((_, textField) => void new fabric.TextField(textField));
  $('.ms-CheckBox').each((_, button) => void new fabric.CheckBox(button));
  $('.ms-Button').each((_, button) => void new fabric.Button(button));
  // tslint:enable:no-unused-expression

  const params = getUrlParams(window.location.search) as Parameters;
  $dialog.find('#app-Author').val(params.userName);
  if (params.canUpdateStatus !== 'true') {
    $dialog.find('#app-StatusCompleted').addClass('is-disabled');
  }
  if (params.canUpdateStatusNotes !== 'true') {
    $dialog.find('#app-StatusNotes')
      .prop('disabled', true)
      .closest('.ms-TextField').find('label').addClass('is-disabled');
  }
  $dialog.show();

  const validator = $dialog.validate({
    submitHandler: form => {
      showOverlay($dialog);
      setTimeout(() => {
        const $form = $(form);
        const saveInfo: SaveInfo = {
          author: $form.find('#app-Author').val() as string,
          comments: $form.find('#app-Comments').val() as string,
          publish: $form.find('#app-Publish').hasClass('is-checked'),
          status: $form.find('#app-StatusCompleted').hasClass('is-checked') && 'Completed',
          statusNotes: $form.find('#app-StatusNotes').val() as string
        };

        validator.destroy();
        sendMessage('save', saveInfo);
      }, 0);
    }
  });

  $dialog.find('#app-CancelButton').on('click', () => {
    showOverlay($dialog);
    setTimeout(() => {
      validator.destroy();
      sendMessage('dialogClosed');
    }, 0);
  });
};
