import { Authenticator, AuthError, IEndpointConfiguration, IToken, Utilities } from '@microsoft/office-js-helpers';

const clientId = 'c30d1e23-a448-4687-9446-938ea15f65f0';
const clientSecret = 'jyzdXGM68:*%crpIQDT708;';

// @ts-ignore
export class MicrosoftAuthenticator extends Authenticator {
  constructor() {
    super();

    this.endpoints.add('OneDrive', {
      authorizeUrl: '/oauth20_authorize.srf',
      baseUrl: 'https://login.live.com',
      clientId,
      redirectUrl: `${window.location.origin}/microsoft-token-callback.html`,
      responseType: 'token',
      scope: 'files.read',
      state: true
    });
  }

  public async logonAsync(provider: string = 'OneDrive'): Promise<string> {
    return await this.authenticate(provider)
      .then(token => token.access_token)
      .catch(error => {
        Utilities.log(error);
        return null;
      });
  }
}
