import { Storage, StorageType, Utilities as OfficeUtilities } from '@microsoft/office-js-helpers';
import { MicrosoftGrapApi } from './microsoft-graph-api';

interface Properties {
  [key: string]: any;
}

export async function getPropertiesAsync(): Promise<Properties> {
  const cache = new Storage<Properties>('DocumentPropertiesCache', StorageType.SessionStorage);

  let properties = cache.get('properties');
  if (properties && properties['OFFICE_CTX_Url']) {
    return properties;
  }

  properties = {};
  properties['OFFICE_CTX_ContentLanguage'] = Office.context.contentLanguage;
  properties['OFFICE_CTX_DisplayLanguage'] = Office.context.displayLanguage;
  properties['OFFICE_CTX_IsAddin'] = OfficeUtilities.isAddin;
  properties['OFFICE_CTX_IsIEOrEdge'] = OfficeUtilities.isIEOrEdge;
  properties['OFFICE_CTX_Host'] = Office.context.host;
  properties['OFFICE_CTX_Mode'] = Office.context.document.mode;
  properties['OFFICE_CTX_OfficeTheme'] = Office.context.officeTheme;
  properties['OFFICE_CTX_Platform'] = Office.context.platform;
  properties['OFFICE_CTX_TouchEnabled'] = Office.context.touchEnabled;
  properties['OFFICE_CTX_Url'] = Office.context.document.url;
  properties['OFFICE_CTX_Version'] = Office.context.diagnostics.version;

  const url = new URL(properties['OFFICE_CTX_Url']);
  properties['OFFICE_CTX_FileName'] = getFileName(url);
  properties['OFFICE_CTX_FilePath'] = getFilePath(url);

  // await getSettingsAsync();

  switch (properties['OFFICE_CTX_Host']) {
    case Office.HostType.Excel:
      await loadExcelPropertiesAsync(properties);
      break;

    case Office.HostType.Word:
      await loadWordPropertiesAsync(properties);
      break;

    case Office.HostType.PowerPoint:
      await loadPowerPointPropertiesAsync(properties);
      break;
  }

  await loadPropertiesFileAsync(properties);

  cache.set('properties', properties);
  return properties;
}

function getFileName(url: URL): string {
  return url.pathname.split('/').pop();
}

function getFilePath(url: URL): string {
  const path = url.pathname.split('/');
  path.splice(1, 1);
  return path.join('/');
}

async function loadExcelPropertiesAsync(properties: Properties): Promise<void> {
  try {
    await Excel.run(async context => {
      context.workbook.load('name');
      const customProperties = context.workbook.properties.custom;
      customProperties.load('key,type,value');
      await context.sync();

      for (const property of customProperties.items) {
        properties[property.key] = property.value;
      }
    });
  }
  catch (error) {
    // Ignore.
  }
}

function loadPowerPointPropertiesAsync(properties: Properties): Promise<void> {
  return Promise.resolve();
}

async function loadPropertiesFileAsync(properties: Properties): Promise<void> {
  await new Promise(async resolve => {
    let content = '';
    try {
      const graphApi = new MicrosoftGrapApi();
      content = await graphApi.getFileContentAsync(`${properties['OFFICE_CTX_FilePath']}.props`) as string;
    }
    catch (error) {
      // Ignore.
    }
    if (content && content.length && content[0] === '{') {
      const props = JSON.parse(content);
      for (const name of Object.keys(props)) {
        const value = props[name];
        if (value !== null) {
          properties[name] = value;
        }
      }
    }

    resolve();
  });
}

async function loadSettingsAsync(properties: Properties): Promise<void> {
  try {
    await new Promise(resolve => {
      Office.context.document.settings.refreshAsync(asyncResult => {
        if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
          const settings = asyncResult.value;

          ['SGM_SYS_ActivityID',
            'SGM_SYS_DeliverableID',
            'SGM_SYS_GateDocumentID',
            'SGM_SYS_ProjectID',
            'SGM_SYS_RelatedFileID',
            'SGM_SYS_Server',
            'SGM_SYS_TemplateLibraryID'].forEach(name => {
              const value = settings.get(name);
              if (value !== null) {
                properties[name] = value;
              }
            });
        }

        resolve();
      });
    });
  }
  catch (error) {
    // ignore
  }
}

async function loadWordPropertiesAsync(properties: Properties): Promise<void> {
  try {
    await Word.run(async context => {
      const customProperties = context.document.properties.customProperties;
      customProperties.load('key,type,value');
      await context.sync();
      for (const property of customProperties.items) {
        properties[property.key] = property.value;
      }
    });
  }
  catch (error) {
    // Ignore.
  }
}
