import { Utilities as OfficeUtilities } from '@microsoft/office-js-helpers';
import * as $ from 'jquery';
import { fabric } from './office-ui-fabric';
import { sleep } from './utilities';

export declare type MessageType = 'credentials' | 'dialogClosed' | 'dialogResult' | 'error' | 'save';

export declare type NotifyType = 'default' | 'success' | 'error' | 'warning' | 'severe-warning';

export interface DialogResult {
  type: MessageType;
  context?: any;
  data: any;
  dialog?: Office.Dialog;
}

export function logMessage(message: string | Error | OfficeHelpers.CustomError, type: NotifyType = 'default'): void {
  if (typeof message === 'string') {
    switch (type) {
      case 'error':
        OfficeUtilities.log(message);
        break;

      case 'warning':
      case 'severe-warning':
        console.warn(message);
        break;

      default:
        // tslint:disable-next-line:no-console
        console.info(message);
    }
  }
  else {
    OfficeUtilities.log(message);
  }
}

export function sendMessage(type: MessageType, data: any = null): void {
  Office.context.ui.messageParent(JSON.stringify({ type, data }));
}

export async function showDialogAsync(url: string, height: number = 30, width: number = 20, context?: any, keepOpen: boolean = false): Promise<DialogResult> {
  let result = await showDialogInternalAsync(url, height, width, context, keepOpen);

  let retryCount = 0;
  const maxRetries = 10;
  while (result.type === 'error' && result.data.code === 12007 /* dialog box is already opened from this host window */ && retryCount < maxRetries) {
    retryCount++;
    // tslint:disable-next-line:no-console
    console.log(`showDialogAsync retry: ${retryCount}`);
    await sleep(200);
    result = await showDialogInternalAsync(url, height, width, context, keepOpen);
  }

  return result;
}

export async function showNotificationAsync(message: string | Error | OfficeHelpers.CustomError, type: NotifyType = 'default', title?: string): Promise<void> {
  logMessage(message, type);

  const messageText = typeof message === 'string' ? message : message.message;
  const url = `/notification-dialog.html?message=${encodeURIComponent(messageText.replace('\n', '¦'))}&title=${encodeURIComponent(title || '')}&type=${type}`;

  await showDialogAsync(url, 20, 16);
}

export function showOverlay($dialog: JQuery) {
  $dialog
    .off('click.showOverlay')
    .on('click.showOverlay', clickHandler);

  const $overlay = $('.ms-Overlay')
    .css('z-index', 9999)
    .off('click.showOverlay')
    .on('click.showOverlay', clickHandler);

  new fabric.Overlay($overlay[0]).show();

  function clickHandler(event: Event): boolean {
    event.preventDefault();
    event.stopPropagation();
    event.stopImmediatePropagation();
    showOverlay($dialog);
    return false;
  }
}

function parseMessage(arg: { message: boolean | string }): DialogResult {
  if (typeof arg.message === 'string') {
    return JSON.parse(arg.message);
  }
  else {
    return { type: 'dialogResult', data: arg.message };
  }
}

function showDialogInternalAsync(url: string, height: number, width: number, context: any, keepOpen: boolean): Promise<DialogResult> {
  return new Promise<DialogResult>(resolve =>
    Office.context.ui.displayDialogAsync(
      `${window.location.origin}${url}`,
      { asyncContext: context, displayInIframe: true, height, width },
      asyncResult => {
        if (asyncResult.status === Office.AsyncResultStatus.Failed) {
          resolve({ type: 'error', context, data: asyncResult.error });
          return;
        }

        const dialog = asyncResult.value;
        dialog.addEventHandler(Office.EventType.DialogEventReceived, () => {
          if (!keepOpen) {
            dialog.close();
          }
          resolve({ type: 'dialogClosed', context, data: null, dialog });
        });

        dialog.addEventHandler(Office.EventType.DialogMessageReceived, (arg: { message: boolean | string }) => {
          if (!keepOpen) {
            dialog.close();
          }
          const message = parseMessage(arg);
          message.context = context;
          message.dialog = dialog;
          resolve(message);
        });
      }
    ));
}
